.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  margin-bottom: 20px;
  display: grid;
}

.photo-card {
  flex-direction: column;
  align-items: center;
  padding: 10px;
  display: flex;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.info {
  text-align: center;
}

.load-more {
  margin-top: 20px;
  display: none;
}

.search-form {
  width: 100%;
  justify-content: center;
}

